@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap");
html {
  scroll-behavior: smooth;
}
body {
  font-family: "Montserrat", sans-serif !important;
  font-weight: 500;
  input{
    color: #000 !important;
    &::placeholder{
      color: #000 !important;
    }
  }
  .video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
  }
  a {
    color: #fff;
    text-decoration: none;
    &.btn-default {
      background-image: url(../../img/Public/btn_bg.png);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      // background-color: #ee4c89;
      // border: 1px solid #ee4c89;
      border-radius: 10px;
      padding: 5px 30px;
      font-size: 20px;
      font-weight: 500;
    }
  }
}
header#header {
  background-color: #ee4c89;
  padding: 5px 70px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1050;
  nav.navbar {
    a.navbar-brand {
      width: 20%;
      padding: 0;
      img {
        width: 90%;
      }
    }
    ul.navbar-nav {
      li {
        a {
          font-size: 14px;
          padding: 0px 10px;
          font-weight: 400;
          img {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
  }
}
main#main_content {
  .home_banner {
    .carousel-caption {
      top: 50%;
      transform: translateY(-50%);
      h2 {
        color: black;
        font-family: "Montserrat", sans-serif;
        font-size: 55px;
        font-weight: 800;
        text-align: left;
      }
      p {
        text-align: left;
        font-size: 20px;
        margin-top: 20px;
        font-weight: 500;
        color: black;
      }
    }
  }
  .other_banner{
    p {
      text-align: left;
      font-size: 20px;
      margin-top: 20px;
      font-weight: 500;
      color: black;
    }
  }
  .homebanner {
    .carousel-caption {
      top: 50%;
      transform: translateY(-50%);
      h2 {
        color: black;
        font-family: "Montserrat", sans-serif;
        font-size: 55px;
        font-weight: 800;
        text-align: left;
      }
      p {
        text-align: left;
        font-size: 20px;
        margin-top: 20px;
        font-weight: 500;
        color: black;
      }
    }
  }
  .bigVideo {
    padding: 70px 0;
    .video_cover {
      position: relative;
      display: block;
      // padding: 56px 0;
    }
    .button_cover {
      position: absolute;
      bottom: 80px;
      left: 0;
      right: 0;
    }
  }
  .latest_posts {
    background-color: #f6f6f6;
    padding: 50px 0;
    .form_box {
      .form-group {
        margin: 0 0 30px;
      }
      label {
        font-size: 20px;
        color: #9d9d9d;
      }
      .form-control {
        height: 35px;
        &::placeholder {
          color: #c7c7c7;
        }
      }
      select {
        color: #c7c7c7;

        option {
          color: #c7c7c7;
        }
      }
      .btn {
        font-size: 18px;
        // color: #828282 !important;
      }
      input[type="submit"] {
        background-image: url(../../img/Public/btn_bg.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        color: #fff;
        border-radius: 10px;
        display: block;
        width: 280px;
        padding: 5px 30px;
        font-size: 20px;
        font-weight: 500;
        margin: 20px 0 auto;
      }
    }
    // .owl-dots {
    //   text-align: center;
    //   button.owl-dot {
    //     width: 10px;
    //     height: 10px;
    //     background: #d9d9d9 !important;
    //     border-radius: 50%;
    //     margin: 0 4px;
    //     &.active {
    //       background: #959595 !important;
    //     }
    //   }
    // }
    &.v2 {
      .card {
        .card-body {
          h2 {
            font-size: 25px;
          }
        }
      }
    }
    h1 {
      text-transform: uppercase;
      color: #ff5ec1;
      margin: 0 0 30px;
    }
    .btn-default {
      font-size: 16px;
      padding: 5px 15px;
    }
    .card {
      background-color: transparent;
      border: 0;
      margin: 0 0 40px;
      .card-body {
        padding: 0;
        h2 {
          color: #696969;
        }
        p {
          font-size: 15px;
          color: #696969;
        }
        h5 {
          font-weight: 400;
          font-size: 18px;
          color: #696969;
        }
      }
    }
  }
}
footer#footer {
  background-color: #ee4c89;
  padding: 70px 0 100px;
  .social {
    margin-top: 25px;
    li {
      margin: 0 5px;
      a {
        width: 40px;
        padding: 0%;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 40px;
        font-size: 16px;
        background-color: #fff;
        border-radius: 50%;
        color: #ee4c89;
        border: 1px solid #fff;
        transition: 0.5s all;
        &:hover {
          transition: 0.5s all;

          background-color: transparent;
          border-radius: 50%;
          color: #fff;
          border-color: #fff;

          svg {
            color: #fff;
          }
        }
        svg {
          color: #ee4c89;
        }
      }
    }
  }
  p {
    color: #fff;
    font-size: 15px;
  }
}
.lim {
  position: fixed;
  z-index: 2000;
  bottom: 100px;
  left: 0;
  right: 0;
  width: 100%;
  text-align: center;
}
.link {
  background-color: #ff5ec1 !important;
  border-radius: 13px;
  font-size: 15px;
  color: #fff !important;
  text-decoration: none;
}
// profile page
.section-1 {
  .card {
    border: 0;
    padding-top: 20px;
    .card-body {
      text-align: center;
      .img_cover {
        position: relative;
        display: inline-block;
        margin: 0 auto;
        .Shape {
          position: absolute;
          right: 0;
          bottom: 0;
        }
      }
    }
    h4 {
      font-size: 21px;
      font-weight: 500;
      color: #4f4f4f;
      margin-top: 15px;
      text-align: center;
    }
    .p_1 {
      font-size: 13px;
      text-align: center;
      color: #828282;
      font-weight: 500;
    }
  }
  .row_2 {
    display: flex;
    justify-content: center;
    text-align: center;
    .p_2 {
      color: #828282;
      font-size: 14px;
    }
  }
  .back {
    // background-image: url(../../img/Public/bg_2.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 50px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
  }
  .p_3 {
    margin-bottom: 0;
    margin-left: 20px;
    align-self: center;
  }
  .gh {
    padding-top: 60px;
  }
  .hk {
    padding-bottom: 100px;
  }
}

// events page
.section-2 {
  padding: 50px 0;
  .card {
    border: 0;
    background-color: transparent;

    .img_cover {
      position: relative;
    }
    .Shape {
      position: absolute;
      right: 0;
      left: 0;
      text-align: center;
      bottom: -15px;
    }
    a {
      color: #fff !important;
      text-decoration: none;
      &.btn-default {
        background-image: url(../../img/Public/btn_bg.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        border-radius: 5px;
        padding: 5px 30px;
        font-size: 14px;
        font-weight: 500;
      }
    }
  }
  .pa {
    padding: 40px 0;
    p {
      font-size: 15px;
      color: #4f4f4f;
    }
    h4 {
      color: #4f4f4f;
      font-size: 24px;
      margin-bottom: 20px;
    }
  }
  .nav {
    justify-content: flex-end;
  }
  .h2 {
    font-size: 23px;
    color: #4f4f4f;
    margin: 20px 0;
  }
}
// about_us page
.section-3 {
  padding: 100px 0;
  h1 {
    font-size: 58px;
    font-weight: 700;
  }
  h3 {
    font-size: 45px;
    font-weight: 700;
    color: #3c7b93;
  }
  h4 {
