// selection_process page
.section-4 {
  padding: 70px 0 0;
  padding-bottom: 50px;
  .p_1 {
    font-size: 16px;
    margin: 0;
  }
  .h6-1 {
    font-size: 17px;
    color: black;
    font-weight: 500;
  }
