    font-size: 13px;
  }
  .p-9 {
    font-size: 15px;
  }
  ul {
    li {
      font-size: 14px;
      ::before {
        content: "";
        width: 2%;
        height: 2%;
        top: 5px;
        left: 0;
        border-radius: 50%;
      }
    }
  }
  .h4-2 {
    font-size: 40px;
    font-weight: 700;
    color: #3c7b93;
  }
}
// participate page
.section-6 {
  padding: 100px 0;
  .div1 {
    width: 20px;
    height: 20px;
    font-size: 18px;
    font-weight: 600;
    color: white;
    border-radius: 50%;
    background: #ff5ec1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 40px auto 0;
  }
  .div2 {
    color: #d3d3d3;
    h5 {
      color: #d3d3d3;
    }
  }
  .p1 {
    font-size: 10px;
  }
  .p2 {
    font-size: 11px;
  }
  ul {
    li {
      font-size: 11px;
      ::before {
        content: "";
        width: 1%;
        height: 1%;
        top: 5px;
        left: 0;
        right: 0;
        border-radius: 50%;
      }
    }
  }
  .img {
    max-height: 388px;
    width: 100%;
  }
  .h51 {
    font-size: 15px;
    font-weight: 700;
    color: #3c7b93;
    margin: 15px 0 0 0;
  }
}
// news page
.section-7 {
  padding: 50px 0;
  .card {
    background-image: url(https://dummyimage.com/200x300/000/000);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border: 0;
    margin: 0 0 40px;
    border-radius: 0;
    .card-body {
      padding: 0;
      padding-top: 20px;
      h2 {
        font-size: 31px;
        margin-left: 15px;
        margin-top: 30px;
        color: white;
      }
      .read {
        background-color: #fff;
        border-radius: 50px;
        color: #000 !important;
        margin-left: 58px;
        font-size: 12px;
        margin-bottom: 20px;
      }
    }
  }
  .owl-nav {
    text-align: center;
    button.owl-prev {
      left: -40px;
      height: 100%;

      text-align: left;
    }
    button.owl-next {
      right: -40px;
      height: 100%;

      text-align: right;
    }
    button {
      position: absolute;
      top: 0;

      span {
        font-size: 60px;
      }
    }
  }
}
.section-8 {
  padding-bottom: 50px;
  video {
    width: 100%;
  }
  .owl-nav {
    text-align: center;
    button.owl-prev {
      left: -40px;
      height: 100%;

      text-align: left;
    }
    button.owl-next {
      right: -40px;
      height: 100%;

      text-align: right;
    }
    button {
      position: absolute;
      top: 0;

      span {
        font-size: 60px;
      }
    }
  }
}
